<template>
  <table class="table table-bordered table-responsive table-dark border border-danger">
    <thead>
      <tr class="text-secondary">
        <th scope="col" class="fit" style="width:5%;">#</th>
        <th scope="col">İşletme Adı</th>
        <th scope="col" class="fit text-center" colspan="3">Ayrıntılar</th>
      </tr>
    </thead>
    <tbody>
      <tr v-show="!IsletmelerLoadOk">
        <th class="text-center mx-auto" colspan="3">
          <is-loading class="text-white" />
        </th>
      </tr>
      <table-row
        v-show="IsletmelerLoadOk"
        v-for="(item, index) in Isletmeler"
        :IsletmeAdi="item.IsletmeAdi"
        :Sira="(index+1)"
        :Id="item._id"
        :key="item.Id"
      />
      <tr>
        <th colspan="5" class="text-center">
          <router-link class="btn btn-lg btn-info" to="/Isletmeler/Ekle">
            <i class="fa fa-plus mr-2" style="font-size: 1.2em;"></i>Ekle
          </router-link>
        </th>
      </tr>
    </tbody>
  </table>
</template>

<script>
import TableRow from "../../components/Isletmeler-Default-TableRow.vue";
import isLoading from "../../components/isLoading.vue";

export default {
  name: "Isletmeler-Default",
  data: () => {
    return {
      Isletmeler: {},
      IsletmelerLoadOk: false
    };
  },
  created() {
    this.getData();
  },
  components: {
    TableRow,
    isLoading
  },
  computed: {},
  methods: {
    getData() {
      this.IsletmelerLoadOk = false;
      fetch(process.env.VUE_APP_SERVER)
        .then(res => res.json())
        .then(data => {
          this.Isletmeler = data;
          this.IsletmelerLoadOk = true;
        });
    }
  }
};
</script>

